import Message from "./Message/Message";
import style from "./Messages.module.css";
import React from "react";
import { reduxForm } from "redux-form";
import { maxLength, required } from "../../utilits/validation/Validate";
import { CreateField, Textarea } from "../../common/formsControl/FormsControl";


const Messages = ({messagePage}) => {
  let sendMessage = (values) => {
    sendMessage(values.newMessage);
  };

  return (
    <div className={style.dialog}>
      {messagePage.userMessages.map((m) => (<Message userMessage={m.userMessage} key={m.id} />))}
      <AddMessageReduxForm onSubmit={sendMessage}/>
    </div>
  );
};
const maxLength10 = maxLength(10);

const MessageForm = ({handleSubmit}) => {
  return (
    <form className={style.newMessage} onSubmit={handleSubmit}>
      {CreateField({name:'newMessage', placeholder:"Your message!", component:Textarea, validate:[required, maxLength10]})}
      <button className={style.sendMessage}>SEND</button>
    </form>
  );
}

const AddMessageReduxForm = reduxForm({form: 'addMessage'})(MessageForm)

export default Messages;
