import { NavLink } from "react-router-dom";
import style from "./DialogListItem.module.css";

const DialogsListItem = ({id, img, surname, name}) => {
    return (
        <NavLink to={`/dialogs/${id}`} activeClassName={style.active} className={style.dialogsListItem}>
            <img src={img} alt="#" />
            <p className={style.surname}>{surname}</p>
            <p className={style.name}>{name}</p>
        </NavLink>
    )
}

export default (DialogsListItem);