import style from "./DialogList.module.css";
import DialogsListItem from "./DialogListItem/DialogListItem";

const DialogList = ({userDialogs}) => {
    return (
        <div className={style.dialogList}>
            { userDialogs.map( el => <DialogsListItem id={el.id} key={el.id} img={el.img} name={el.name} surname={el.surname} />) }
        </div>
    )
}

export default (DialogList);